    //extend inherits functionality from the constructor
    class Animal {
        constructor(){
            console.log('an animal')
        }

        speak(sound){
            console.log("an animal says ..." + sound)
        }
    }

    class Dog extends Animal {
        constructor(){
            super()
            console.log('a dog')
        }

        bark(){
            this.speak('bark bark')
        }
    }

    class cat extends Animal {
        constructor(){
            super()
            console.log('a cat')
        }

        meow(){
            console.log('meoow meow')
        }
    }



